@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
/* Global Styles */
:root {
  --primary-color: #17a2b8;
  --green-color: #63916a;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
  --quote-color: #CDDEE7;
  --insight-color: #DFCDDF;
  --question-color: #F8E5E4;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
  background-color: #F6F6F7;
}

input[type='text'],
input[type='submit'],
button {
  font-family: inherit;
}

a {
  color: #17a2b8;
  color: var(--primary-color);
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utilities */
.container {
  max-width: 1100px;
  height: 100%;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 5rem;
  margin-bottom: 3rem;
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #17a2b8;
  color: var(--primary-color);
}

.text-dark {
  color: var(--dark-color);
}

