/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 8rem;
  }

  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  /* Profiles Page */
  .profile {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .profile ul {
    display: none;
  }

  /* Profile Page */

  .profile-top img,
  .profile img {
    width: 200px;
    margin: auto;
  }

  .profile-grid {
    grid-template-areas:
      'top'
      'about'
      'exp'
      'edu'
      'github';
  }

  .profile-about .skills {
    flex-direction: column;
  }

  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem;
  }

  .post {
    grid-template-columns: 1fr;
  }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }
}

/* Heading */
.title {
  margin-top: 2rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  color: rgb(77, 77, 77);
  cursor: default;
}

.left-align {
  text-align: left;   
}
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 3rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    opacity: 0.9;
}

.navbar ul {
    display: flex;
}

.navbar a {
    color: #fff;
    margin-left: 1.5rem;
    padding: 0.45rem;
    position: relative;
}

.navbar .menu-link a::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 0.17rem;
    left: 0;
    bottom: 0;
    background: white;

    -webkit-transform: scale(0, 1);

            transform: scale(0, 1);
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
    transition: -webkit-transform 0.4s ease;
    transition: transform 0.4s ease;
    transition: transform 0.4s ease, -webkit-transform 0.4s ease;
}

.navbar .menu-link a:hover::after{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
}
